<template>
  <div class="squareShow" v-if="type == 0">
    <div :class="[direction == 0 ? 'horizontalParent' : 'verticalParent']">
      <template v-for="item in datas" :key="item.id">
        <div class="img_word_item">
          <router-link to="/GameDetails"> <img :src="item.url" /></router-link>
          <h6 v-if="caption != 'none'">{{ item.name }}</h6>
        </div>
      </template>
    </div>
  </div>
  <div class="rectShow" v-if="type == 1">
    <div :class="[direction == 0 ? 'horizontalParent' : 'verticalParent']">
      <template v-for="item in datas" :key="item.id">
        <div class="img_word_item">
          <router-link to="/GameDetails"> <img :src="item.url" /></router-link>
          <h6 v-if="caption != 'none'" :style="{ textAlign: caption }">
            {{ item.name }}
          </h6>
          <div v-if="timer" class="timerBtn">
            {{ item.time ?? "00:10:22:33" }}
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    type: { default: 0, type: Number }, //0:正方形,1:長方形
    icon: { default: false, type: Boolean }, //顯示裝置
    timer: { default: false, type: Boolean }, //倒數時間
    caption: { default: "center", type: String }, //標題
    direction: { default: 0, type: Number }, //0:橫,1:縱
    datas: {
      default: function () {
        var ary = [{ id: "test", url: "@/assets/squarePIC.png" }];
        return ary;
      },
      type: Object,
    }, //array [{url:xxx}]
  },
  computed: {
    // getCaptionPos() {
    //   if (this.caption == "left")
    //     return {
    //       "text-align": this.caption,
    //     };
    // },
  },
};
</script>

<style scoped>
.horizontalParent {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: center;
  align-items: center;
}
.verticalParent {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-content: center;
  align-items: center;
}
.squareShow img {
  max-width: 150px;
  box-sizing: border-box;
  border-radius: 16px;
}
.rectShow img {
  max-width: 340px;
  box-sizing: border-box;
  border-radius: 6px;
}
.rectShow {
  height: 100%;
}
.timerBtn {
  background-color: #ff5187;
  border: none;
  color: white;
  padding: 3px 0;
  border-radius: 6px;
  font-weight: bold;
}
</style>
