        <i id="boot-icon" class="bi bi-caret-right-fill"></i>
      </h5>
    </div>
    <hr /> -->
    <div class="news_list_group">
      <template v-for="(newItem, index) in datas" :key="newItem.id">
        <div>
          <img :src="newItem.thumbnail" />
          <h5 class="category">{{ "【" + newItem.category + "】" }}</h5>
          <h5 class="title">{{ newItem.title }}</h5>
          <h5 class="time">{{ getDate(newItem.posted_at) }}</h5>
        </div>
        <hr v-if="index != datas.length - 1" />
      </template>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      datas: [
        {
          id: 15,
          category: "平台公告",
          title: "公告15號",
          thumbnail: "http://test.url/news/15/thumbnail.png",
