          title: "公告14號",
          thumbnail: "http://test.url/news/14/thumbnail.png",
          posted_at: 1665450530,
        },
        {
          id: 13,
          category: "遊戲‧遊戲",
          title: "公告13號",
          thumbnail: "http://test.url/news/13/thumbnail.png",
          posted_at: 1665440530,
        },
      ],
    };
  },
  methods: {
    getDate: function (timestamp) {
      var date = new Date(timestamp);
      //   console.log(
      //     "Date: " +
      //       date.getDate() +
      //       "/" +
      //       (date.getMonth() + 1) +
      //       "/" +
      //       date.getFullYear() +
      //       " " +
      //       date.getHours() +
      //       ":" +
      //       date.getMinutes() +
      //       ":" +
      //       date.getSeconds()
      //   );
      return (
        date.getMonth() +
        1 +
        "/" +
        date.getDate() +
        " " +
        date.getHours() +
        ":" +
        date.getMinutes()
      );
    },
  },
};
</script>

